.cssload-loader {
  position: relative;
  left: calc(50% - 68px);
  width: 136px;
  height: 136px;
  border-radius: 50%;
  -o-border-radius: 50%;
  -ms-border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  perspective: 1700px; }

.preloader {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-grow: 1;
  justify-content: center;
  flex-direction: column;
  background-image: url("img/preloader-bg.jpg");
  background-position: cover; }

.percents {
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%); }

.cssload-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  border-radius: 50%;
  -o-border-radius: 50%;
  -ms-border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%; }

.cssload-inner.cssload-one {
  left: 0%;
  top: 0%;
  animation: cssload-rotate-one 0.9s linear infinite;
  -o-animation: cssload-rotate-one 0.9s linear infinite;
  -ms-animation: cssload-rotate-one 0.9s linear infinite;
  -webkit-animation: cssload-rotate-one 0.9s linear infinite;
  -moz-animation: cssload-rotate-one 0.9s linear infinite;
  border-bottom: 6px solid white; }

.cssload-inner.cssload-two {
  right: 0%;
  top: 0%;
  animation: cssload-rotate-two 0.9s linear infinite;
  -o-animation: cssload-rotate-two 0.9s linear infinite;
  -ms-animation: cssload-rotate-two 0.9s linear infinite;
  -webkit-animation: cssload-rotate-two 0.9s linear infinite;
  -moz-animation: cssload-rotate-two 0.9s linear infinite;
  border-right: 6px solid white; }

.cssload-inner.cssload-three {
  right: 0%;
  bottom: 0%;
  animation: cssload-rotate-three 0.9s linear infinite;
  -o-animation: cssload-rotate-three 0.9s linear infinite;
  -ms-animation: cssload-rotate-three 0.9s linear infinite;
  -webkit-animation: cssload-rotate-three 0.9s linear infinite;
  -moz-animation: cssload-rotate-three 0.9s linear infinite;
  border-top: 6px solid white; }

@keyframes cssload-rotate-one {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg); }
  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg); } }

@-o-keyframes cssload-rotate-one {
  0% {
    -o-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg); }
  100% {
    -o-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg); } }

@-ms-keyframes cssload-rotate-one {
  0% {
    -ms-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg); }
  100% {
    -ms-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg); } }

@-webkit-keyframes cssload-rotate-one {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg); }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg); } }

@-moz-keyframes cssload-rotate-one {
  0% {
    -moz-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg); }
  100% {
    -moz-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg); } }

@keyframes cssload-rotate-two {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg); }
  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg); } }

@-o-keyframes cssload-rotate-two {
  0% {
    -o-transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg); }
  100% {
    -o-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg); } }

@-ms-keyframes cssload-rotate-two {
  0% {
    -ms-transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg); }
  100% {
    -ms-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg); } }

@-webkit-keyframes cssload-rotate-two {
  0% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg); }
  100% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg); } }

@-moz-keyframes cssload-rotate-two {
  0% {
    -moz-transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg); }
  100% {
    -moz-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg); } }

@keyframes cssload-rotate-three {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg); }
  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg); } }

@-o-keyframes cssload-rotate-three {
  0% {
    -o-transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg); }
  100% {
    -o-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg); } }

@-ms-keyframes cssload-rotate-three {
  0% {
    -ms-transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg); }
  100% {
    -ms-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg); } }

@-webkit-keyframes cssload-rotate-three {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg); }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg); } }

@-moz-keyframes cssload-rotate-three {
  0% {
    -moz-transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg); }
  100% {
    -moz-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg); } }

html,
body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  height: 100%; }

.wrapper {
  display: none; }

.welcome-page {
  position: relative;
  height: 100vh;
  align-items: center;
  background-size: cover;
  display: flex;
  flex-direction: column; }
  .welcome-page .content {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    flex-direction: column; }

.parallax {
  height: 100vh;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -2; }
  .parallax .parallax__layer {
    position: absolute;
    height: 100vh;
    width: 120vw;
    margin-left: -5vw;
    bottom: 0;
    transition: all .2s linear; }
    .parallax .parallax__layer .parallax__image {
      display: block;
      position: absolute;
      bottom: 0;
      width: 120vw;
      height: 100vh;
      margin-left: -10vw; }

.copyright {
  flex: 0 0 auto; }

.card {
  min-width: 20rem;
  text-align: center;
  border-radius: .3125rem;
  overflow: hidden;
  display: none; }

.card__main {
  background-color: rgba(246, 247, 246, 0.7); }

.card__avatar {
  margin: auto;
  padding-top: 2rem;
  padding-bottom: 2rem; }

.card__avatar_img {
  width: 15.625rem;
  border-radius: 18.75rem; }

.card__name {
  font-size: 1.4rem; }

.card__description {
  padding-top: .625rem; }
  .card__description .card__description_paragraph {
    margin-top: 0;
    margin-bottom: 0; }

.card__socials .socials {
  display: flex;
  list-style: none;
  justify-content: center;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0; }
  .card__socials .socials .socials__item {
    font-size: 1.5rem;
    padding: .625rem .625rem; }
    .card__socials .socials .socials__item i {
      color: #00bfa5; }
    .card__socials .socials .socials__item i:hover {
      opacity: 0.6; }

.welcome-page__nav__list {
  display: flex;
  justify-content: center;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none; }

.welcome-page__nav__item {
  display: flex;
  flex-grow: 1;
  border-left: 1px solid #73cac2;
  border-right: 1px solid #73cac2; }
  .welcome-page__nav__item:first-of-type {
    border-left: none; }
  .welcome-page__nav__item:last-of-type {
    border-right: none; }

.welcome-page__nav__link {
  color: #fff;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: #00bfa5;
  text-decoration: none;
  flex-grow: 1; }
  .welcome-page__nav__link:hover {
    background-color: rgba(0, 191, 165, 0.5);
    transition: 0.5s; }

.copyright {
  color: rgba(246, 247, 246, 0.85);
  font-size: 0.8rem; }

.container {
  margin-right: auto;
  margin-left: auto;
  width: 78rem; }

.header {
  display: flex; }

.header-socials .socials {
  display: flex;
  list-style: none;
  padding-left: 0; }

.header-socials .socials__link {
  color: #fff;
  font-size: 1.5rem;
  padding-right: 1rem; }
  .header-socials .socials__link:hover {
    opacity: .7; }

.flexbox-container {
  display: flex; }

.header-nav {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  align-items: center; }
  .header-nav .header-nav__list {
    display: flex;
    list-style: none;
    padding-left: 0; }
  .header-nav .header-nav__link {
    color: #fff;
    text-decoration: none;
    font-size: 1.1rem;
    margin-left: 1rem;
    padding-bottom: .2rem; }
    .header-nav .header-nav__link:hover {
      border-bottom: 3px solid #ffffff;
      transition: .3s; }

.hero {
  color: #fff;
  height: 100vh;
  position: relative;
  overflow: hidden;
  text-align: center; }
  .hero .hero__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    z-index: -1; }
    .hero .hero__bg img {
      display: block;
      width: 100%; }
  .hero .hero__content {
    display: flex;
    height: 70vh;
    justify-content: center; }
    .hero .hero__content .card {
      margin-top: auto;
      margin-bottom: auto; }
  .hero .card__main {
    background-color: transparent;
    color: #fff; }
  .hero .card__name {
    font-size: 2rem; }
  .hero .card__avatar {
    padding-top: 0;
    padding-bottom: 1%; }
  .hero .card__avatar_img {
    width: 10rem; }

.arrow-down-link {
  display: none;
  text-align: center;
  font-size: 2.5rem;
  color: #fff; }
  .arrow-down-link:hover {
    opacity: .7; }
